.warningSearch {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  margin-bottom: 10px;
}
.warningSearch > div:nth-child(1) {
  margin-right: auto;
}
.rangePicker {
  margin-right: 15px;
}
.contentLeftMenuWrapper {
  display: flex;
}
.leftMenuListContent {
  display: flex;
  flex-direction: column;
  font-size: 16px;
}
.leftMenu {
  background-color: #fafafa;
  padding: 15px;
  margin-top: 10px;
  margin-right: 10px;
  height: 82vh;
  overflow-y: auto;
}

.trendContentWrapper {
  width: calc(100% - 0px);
  padding: 10px;
}
.title {
  margin-bottom: 5px;
  margin-left: 15px;
  color: #333;
  font-size: 16px;
  font-weight: 500;
  padding: 10px 0;
}
.trendItemContent {
  background-color: #fff;
  border-radius: 10px;
}
.rangPicker {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding: 10px 60px 10px;
}
.header {
  display: flex;
  align-items: center;
  padding: 8px 8px 8px 15px;
  color: #333;
  border-bottom: 1px solid #bfbfbf;
}

.header > div:nth-child(2) {
  display: flex;
  align-items: center;
  width: 100%;
  height: 35px;
  margin-left: 5px;
}
.pageInfo {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px;
}
.pageInfo > div:nth-child(1) {
  margin-right: 10px;
}
.panelItem {
  background: #edf0f3;
  border-radius: 4px;
  border: 0;
  overflow: hidden;
  border-bottom: 0 !important;
}
.panelItem > div:nth-child(1) {
  padding: 8px 15px;
}

.panelItem > div:nth-child(2) {
  background-color: #fff !important;
}

.reportContent {
  padding: 15px;
}

.reportTitle {
  display: flex;
  justify-content: center;
  color: #1890ff;
  margin-bottom: 20px;
  height: 40px;
  align-items: center;
  position: relative;
}
.reportTitle > div:nth-child(1) {
  font-size: 22px;
  font-weight: 500;
  color: #333;
}

.reportTitle > div:nth-child(2) {
  position: absolute;
  right: 0;
  display: flex;
  cursor: pointer;
}

.reportTitle > div:nth-child(2) {
  margin-right: 10px;
}
.overviewItemWrapper {
  display: flex;
  justify-content: space-between;
  flex-flow: wrap;
  margin: 10px 0;
}

.overviewItemWrapper > div:nth-child(even) {
  margin-right: 0;
}
.overviewItem {
  display: flex;
  width: 49.5%;
  color: #333;
  margin-bottom: 10px;
}
.overviewItem > div {
  border: 1px solid #e8e8e8;
  padding: 10px;
}

.overviewItem > div:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: none;
  width: 200px;
}

.overviewItem > div:nth-child(2) {
  width: 100%;
}
.planChartWrapper {
  margin-top: 10px;
  width: 100%;
  border: 1px solid #e8e8e8;
  border-radius: 2px;
}
.planChartBlockWrapper {
  display: flex;
  flex-flow: row wrap;
  max-height: 240px;
  overflow-y: auto;
  padding-top: 15px;
  padding: 20px;
}
.stateButton {
  position: relative;
  top: 0px;
  border: 1px solid #333;
  transition: all 0.2s ease-in-out;
  width: 178px;
  margin-right: 32px;
  margin-bottom: 10px;
  height: 32px;
}
.stateButton > div {
  margin: auto;
  width: 100%;
  height: 100%;
  line-height: 30px;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.planChartTitle {
  background-color: #fafbfd;
  font-weight: 500;
  height: 30px;
  line-height: 30px;
  border-bottom: solid 1px #e8e8e8;
}
.planChartTitleCircular {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #54bba6;
  border-radius: 50%;
  margin-right: 10px;
  margin-left: 20px;
}
.topologyWrapper {
  display: flex;
  align-items: center;
  margin-right: 80px;
  margin-bottom: 80px;
}

.topologyChildren {
  display: flex;
  flex-direction: column;
}
.topologyChildren > div:nth-child(1) {
  position: relative;
}
.verticalLine {
  position: absolute;
  background-color: #333;
  left: 0;
  top: 22px;
  height: calc(100% - 55px);
  width: 2px;
  border-radius: 1px;
}
.topologyItem {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border: 2px solid #333;
  border-radius: 5px;
}

.rootItemBox {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.connectLine {
  display: inline-block;
  width: 30px;
  background-color: #333;
  height: 2px;
  border-radius: 1px;
}

.basicCardWrapper {
  display: flex;
  flex-flow: row wrap;
}

.basicCardItem {
  width: 40%;
  padding: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
}
.buttonContainer {
  width: 100%;
}
.greenType {
  background-color: #5ba165;
  color: #fff;
  border-color: #5ba165;
}
.redType {
  background-color: #ff4d4f;
  color: #fff;
  border-color: #ff4d4f;
}
.buttonContainer > button {
  margin-right: 15px;
}
._bigfontSize {
  font-size: 14px;
}
